@charset "UTF-8";
/** Media Queries **/
.wc_decripciones {
  margin-top: 3rem;
}
.wc_decripciones h1 {
  font-size: 2.2rem;
  font-weight: 400;
  margin: 0 0 1.5rem 0;
}
.wc_decripciones h1 span {
  font-weight: 900;
}
@media (min-width: 768px) {
  .wc_decripciones h1 {
    font-size: 2.5rem;
  }
}
.wc_decripciones ul li {
  color: #000000;
}
.wc_decripciones p {
  color: #000000;
  text-align: justify;
  line-height: 1.5;
}
.wc_decripciones p:last-of-type {
  margin: 0;
}

.wc_planes {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  margin-top: 3rem;
}
.wc_planes__simple {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .wc_planes__simple {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.wc_planes__item {
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
  border-radius: 1rem;
  max-width: 32rem;
  min-width: 32rem;
  position: relative;
}
.wc_planes__item:hover {
  cursor: pointer;
}
.wc_planes__item:hover .wc_planes__cursor {
  color: #f2f2f2;
  transform: scale(0.7);
  animation: transform;
  animation-duration: 0.5ms;
  animation-delay: 0.5ms;
}
.wc_planes__cursor {
  color: #FFFFFF;
  position: absolute;
  right: 1rem;
  bottom: 0.5rem;
}
.wc_planes__cursor i {
  font-size: 3rem;
}
.wc_planes__info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1.5rem;
  border-radius: 1rem;
  border: 4px solid #1D57C5;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.wc_planes__info--basico {
  background-color: #5b8fb5;
}
.wc_planes__info--clasico {
  background-image: linear-gradient(90deg, #1D57C5, #1D57C5, #41C1d5);
}
.wc_planes__info--premium {
  background-image: linear-gradient(90deg, #f7841c, #f7841c, #eeb82c);
}
.wc_planes__contenido {
  display: none;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.wc_planes__contenido--clasico {
  background-color: #E0F5FC;
}
.wc_planes__contenido--clasico i {
  background-color: #1D57C5;
}
.wc_planes__contenido--premium {
  background-color: #efe2d3;
}
.wc_planes__contenido--premium i {
  background-color: #f7841c;
}
.wc_planes__icono {
  font-size: 4rem;
}
.wc_planes__datos {
  font-weight: 600;
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
  line-height: 0.8;
  font-size: 5rem;
}
.wc_planes__datos--basico {
  display: flex;
  flex-direction: row-reverse;
}
.wc_planes__datos--basico bdi {
  display: flex;
  align-items: center;
}
.wc_planes__datos--basico .woocommerce-Price-currencySymbol {
  font-size: 2.5rem;
}
.wc_planes__datos--moneda {
  font-size: 2.5rem;
}
.wc_planes__datos--slogan {
  font-size: 2.2rem;
}
.wc_planes__datos del {
  font-size: 2rem;
  color: #cacaca;
}
.wc_planes__datos ins {
  all: unset;
}
.wc_planes__datos ins bdi {
  display: flex;
  align-items: center;
  gap: 0;
}
.wc_planes__datos ins .woocommerce-Price-currencySymbol {
  font-size: 2.5rem;
}
.wc_planes__frase {
  font-size: 1.2rem;
  line-height: 2;
  font-weight: 900;
  letter-spacing: 0.1rem;
  margin-top: 0.6rem;
}
.wc_planes__forma--clasico {
  fill: #41C1d5;
}
.wc_planes__forma--premium {
  fill: #f17708;
}
.wc_planes__ul {
  padding: 1rem 3rem;
  line-height: normal;
}
.wc_planes__li {
  margin: 0.5rem 0;
  color: #000000;
}
.wc_planes__li i {
  border-radius: 50%;
  color: #FFFFFF;
}
.wc_planes__li i::before {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
}
.wc_planes__total {
  font-weight: 600;
  text-align: center;
  font-size: 1.8rem;
}
.wc_planes__total--clasico {
  color: #1D57C5;
}
.wc_planes__total--premium {
  color: #f7841c;
}
.wc_planes__eira {
  padding: 0 3rem;
  display: flex;
  margin-bottom: 2rem;
}
.wc_planes__img {
  width: 7rem;
  z-index: 1;
}
.wc_planes__monto {
  margin-top: 1.5rem;
  flex-grow: 1;
  background-color: #FFFFFF;
  color: #000000;
  position: relative;
  font-weight: 600;
  line-height: normal;
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  align-items: center;
  font-size: 3rem;
  padding: 0 2rem;
  border-bottom-right-radius: 1rem;
  border-top-right-radius: 1rem;
  flex-direction: column-reverse;
}
.wc_planes__monto--clasico {
  color: #1D57C5;
}
.wc_planes__monto--premium {
  color: #f7841c;
}
.wc_planes__monto::before {
  position: absolute;
  left: -3rem;
  display: block;
  content: "";
  width: 3rem;
  height: 100%;
  background-color: #FFFFFF;
}
.wc_planes__monto .woocommerce-Price-currencySymbol {
  font-size: 2.2rem;
}
.wc_planes__monto ins {
  all: unset;
}
.wc_planes__monto del {
  font-size: 2rem;
  color: #7d7d7d;
}
.wc_planes__activo .wc_planes__info--clasico,
.wc_planes__activo .wc_planes__info--premium {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.wc_planes__activo .wc_planes__contenido {
  display: block;
}
.wc_planes__compra {
  display: flex;
  justify-content: center;
}
.wc_planes__boton {
  background-color: #00615A;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_planes__boton {
    width: auto;
  }
}
.wc_planes__boton:hover {
  background-color: #002e2b;
  color: #FFFFFF;
  cursor: pointer;
}
.wc_planes__boton {
  padding: 1rem;
  font-size: 1.5rem;
  margin-top: 1rem;
}

.wc_variaciones {
  color: #000000;
  margin: 0 auto;
}
.wc_variaciones__h3 {
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
  color: #1D57C5;
  margin-top: 2rem;
}
.wc_variaciones__variacion {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .wc_variaciones__variacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .wc_variaciones__variacion {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.wc_variaciones__texto {
  color: #00615A !important;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.wc_variaciones__item {
  min-width: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.wc_variaciones__item--regular {
  background-color: whitesmoke;
  flex: 1;
  border: 1px solid #d1d1d1;
  justify-content: center;
}
.wc_variaciones__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1.2;
  padding: 2rem;
  border: 1px solid #00615A;
}
.wc_variaciones__contenido--regular {
  border: none;
}
.wc_variaciones__contenido--recomendado {
  border-color: #1D57C5;
}
.wc_variaciones__recomendado {
  background-color: #1D57C5;
  color: #FFFFFF;
  text-align: center;
  padding: 0.5rem;
}
.wc_variaciones__h4 {
  margin-top: 0;
}
.wc_variaciones p {
  margin: 0;
}
.wc_variaciones__cuota {
  color: #00615A;
  display: flex;
  justify-content: center;
}
.wc_variaciones__cuota--recomendado {
  color: #1D57C5;
}
.wc_variaciones__precio {
  color: #00615A;
  font-size: 2.2rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.wc_variaciones__precio--recomendado {
  color: #1D57C5;
}
.wc_variaciones__etiqueta {
  font-weight: 500;
  margin-bottom: 1rem;
}
.wc_variaciones__dscto {
  font-size: 2rem;
}
.wc_variaciones__boton {
  background-color: #00615A;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_variaciones__boton {
    width: auto;
  }
}
.wc_variaciones__boton:hover {
  background-color: #002e2b;
  color: #FFFFFF;
  cursor: pointer;
}
.wc_variaciones__boton {
  padding: 0.8rem;
  font-size: 1.5rem;
  width: 100%;
}
.wc_variaciones__boton--recomendado {
  background-color: #1D57C5;
}

.pagina__header {
  background-color: #1D57C5;
  background-image: linear-gradient(to bottom, #1d57c5, rgba(29, 87, 197, 0.4)), image-set(url("../../build/img/header.avif") 1x, url("../../build/img/header.webp") 1x, url("../../build/img/header.jpg") 1x);
