    background-image: linear-gradient(to right, #1d57c5, rgba(29, 87, 197, 0.4)), image-set(url("../../build/img/header.avif") 1x, url("../../build/img/header.webp") 1x, url("../../build/img/header.jpg") 1x);
  }
