.woocommerce span.onsale {
  display: none !important;
}
.woocommerce ul.products {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .woocommerce ul.products {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .woocommerce ul.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.woocommerce ul.products::before {
  display: none !important;
}
.woocommerce ul.products li.product {
  width: 100% !important;
  margin: 0 !important;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 1rem;
}
.woocommerce ul.products li.product img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  margin-bottom: 0 !important;
}

.woocommerce-breadcrumb {
  margin: 0 0 1rem 0 !important;
}

.woocommerce div.product div.summary {
  display: block;
  width: 100%;
  float: none;
  clear: both;
}

.wc_descripcion__descargar {
  display: flex;
  justify-content: center;
}
.wc_descripcion__descargar a {
  background-color: #00615A;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_descripcion__descargar a {
    width: auto;
  }
}
.wc_descripcion__descargar a:hover {
  background-color: #002e2b;
  color: #FFFFFF;
  cursor: pointer;
}
.wc_descripcion p {
  color: #000000;
}

.wc_pagar {
  text-align: center;
}
.wc_pagar__boton {
  background-color: #1D57C5;
  padding: 1.5rem 4rem;
  color: #FFFFFF !important;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_pagar__boton {
    width: auto;
  }
}
.wc_pagar__boton:hover {
  background-color: #164399;
  color: #FFFFFF;
  cursor: pointer;
}

.wc_info {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  color: #000000;
  background-color: #f6d88a;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.wc_titulo {
  display: flex;
  margin-bottom: 1rem;
  padding: 0 1.5rem;
}
@media (min-width: 768px) {
  .wc_titulo {
    height: 7rem;
    overflow: hidden;
  }
}
.wc_titulo__title {
  font-size: 1.6rem;
  font-weight: 500;
  margin: 0 !important;
  text-align: center;
}

.wc_detalles {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  max-width: 40rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .wc_detalles--descripcion {
    margin: initial;
  }
}
.wc_detalles__detalle {
  border-right: 1px solid #cccccc;
  display: flex;
  flex-direction: column;
}
.wc_detalles__detalle:last-of-type {
  border: none;
}
.wc_detalles__icono {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #000000;
  margin: 0.4rem 0 !important;
}
.wc_detalles__icono i {
  width: 3rem;
  color: #1D57C5;
  font-size: 1.8rem;
  text-align: center;
}

.wc_etiqueta {
  background-color: #164399;
}
.wc_etiqueta p {
  color: #FFFFFF;
  padding: 0.6rem;
  font-weight: 500;
  text-align: center;
}

.wc_btn_inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 1.5rem;
}
.wc_btn_inicio--precio {
  color: #1D57C5;
  font-weight: 600;
  line-height: normal;
  display: flex;
  justify-content: center;
  gap: 1rem;
  align-items: center;
  font-size: 3rem;
}
.wc_btn_inicio--precio .woocommerce-Price-currencySymbol {
  font-size: 1.8rem;
}
.wc_btn_inicio--precio ins {
  all: unset;
}
.wc_btn_inicio--precio del {
  font-size: 1.8rem;
  color: #7d7d7d;
}
.wc_btn_inicio--id {
  color: #7d7d7d;
  text-align: center;
  padding-bottom: 1rem;
  font-size: 1.3rem;
}
.wc_btn_inicio--id span {
  font-weight: bold;
}
.wc_btn_inicio__boton {
  padding: 1rem;
  background-color: #a31011;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.wc_btn_inicio__boton:hover {
  background-color: #7e0c0d;
  color: #FFFFFF;
}

.wc_header {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  border-bottom: 1px solid #009489;
  margin-bottom: 2rem;
}
.wc_header__h1 {
  font-size: 2.3rem;
  font-weight: normal;
}
.wc_header__h1 span {
  font-weight: bold;
}
.wc_header .wc_datos {
  order: 2;
}
.wc_header__sku {
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.wc_header__sku span {
  font-weight: 900;
}
.wc_header .wc_imagen {
  order: 1;
}
@media (min-width: 1024px) {
  .wc_header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .wc_header .wc_datos {
    order: 1;
  }
  .wc_header .wc_imagen {
    order: 2;
  }
}
.wc_header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .wc_header__contenido {
    flex-direction: row;
  }
}
.wc_header__coordinador {
  color: #000000;
  margin: 0;
}
.wc_header__coordinador span {
  font-weight: bold;
}

.wc_notificacion {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 3rem;
  color: #FFFFFF;
}
.wc_notificacion p {
  margin: 0 !important;
  line-height: normal;
}
.wc_notificacion--activo {
  background-color: #005c55;
  color: #FFFFFF;
}
.wc_notificacion--inactivo {
  background-color: #a71112;
}
.wc_notificacion--aviso {
  background-color: #25a9e1;
}
.wc_notificacion i {
  font-size: 2.5rem !important;
}
.wc_notificacion__texto {
  color: #FFFFFF;
}

.wc_guiadecompra {
  display: flex;
  justify-content: center;
  font-weight: bold;
  background-color: #f6d88a;
  border-radius: 0.5rem;
  text-align: center;
  padding: 0.5rem;
  color: #000000;
}

.wc_acordion__header {
  font-size: 2.2rem;
  color: #1D57C5;
  font-weight: 500;
  margin-top: 3rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  border-bottom: 1px solid #10306c;
  border-left: 5px solid #10306c;
  border-bottom-left-radius: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.wc_acordion__header:hover {
  cursor: pointer;
}
.wc_acordion__body {
  display: none;
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.wc_acordion__body.active {
  display: block;
}
.wc_acordion__open .wc_acordion__header {
  color: #00615A;
  color: #00615A;
  border-bottom: 1px solid black;
  border-left: 5px solid black;
  font-weight: 600;
}

.wc_temario ul {
  margin: 0;
  padding: 0;
}
.wc_temario ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  padding: 1rem;
  background-color: #f0f0f0;
  line-height: 1.5;
  font-weight: 500;
  color: #000000;
  margin-bottom: 0.5rem;
  border-radius: 0.5rem;
}
.wc_temario ul li::before {
  content: "►";
  font-size: 2rem;
  color: #00615A;
  font-size: 1.5rem;
  margin-right: 1rem;
}
.wc_temario ul li:nth-child(even) {
  background-color: #dedede;
}

.wc_dirigido ul {
  margin: 0;
}
.wc_dirigido ul li {
  color: #000000;
  line-height: 1.5;
  margin-bottom: 0.5rem;
}
.wc_dirigido ul li::marker {
  color: #00615A;
}

.wc_objetivos ul {
  margin: 0;
}
.wc_objetivos ul li {
  color: #000000;
  margin-bottom: 0.5rem;
  line-height: 1.5;
}
.wc_objetivos ul li::marker {
  color: #00615A;
}

.wc_docentes {
  margin-top: 3rem;
}
.wc_docentes__docente {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .wc_docentes__docente {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .wc_docentes__docente {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.wc_docentes__docente ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}
.wc_docentes__item {
  border: 1px solid #cacaca;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0.5rem;
  border-radius: 2rem;
  text-align: center;
}
.wc_docentes__imagen {
  width: 10rem;
  border-radius: 50%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.wc_docentes__label {
  color: #00615A;
  margin: 1rem 0;
  border-bottom: 1px solid #00615A;
}

.wc_calendario {
  display: flex;
  flex-direction: column;
}
.wc_calendario__link {
  margin-bottom: 2rem;
}
.wc_calendario__imagen {
  width: 100%;
}

.buscar {
  width: 100%;
  margin-bottom: 5rem;
  padding: 2rem;
}
.buscar input {
  border: 1px solid #1D57C5 !important;
  color: #1D57C5 !important;
  font-weight: bold !important;
  border-radius: 1rem !important;
}
.buscar input::placeholder {
  color: #1D57C5 !important;
  font-weight: bold !important;
}

.wc_dashboard__user {
  text-align: center;
  font-size: 2.2rem;
  color: #00615A;
  margin-bottom: 2rem;
}
.wc_dashboard__user span {
  font-weight: bold;
  color: #002e2b;
}
.wc_dashboard__titulo {
  text-align: center;
  color: #1D57C5;
  margin-bottom: 0rem;
  font-size: 2rem;
}
.wc_dashboard__grid {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .wc_dashboard__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.wc_dashboard__descripcion {
  font-size: 1.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.incluye {
  margin-bottom: 3rem;
}
.incluye__h2 {
  color: #1D57C5;
  text-align: center;
  margin: 0;
}
.incluye__detalle {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.incluye__campo {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.5rem;
}
.incluye__titulo {
  grid-area: 1/1/2/4;
}
.incluye__clasico {
  grid-area: 1/4/2/5;
}
.incluye__premium {
  grid-area: 1/5/2/6;
}
.incluye__header, .incluye__header--premium, .incluye__header--clasico {
  padding: 1rem;
  font-weight: 900;
  text-align: center;
}
.incluye__header--clasico {
  color: #1D57C5;
}
.incluye__header--premium {
  color: #eeb82c;
}
.incluye__body, .incluye__body--premium, .incluye__body--clasico, .incluye__body--titulo {
  padding: 1rem 1.5rem;
  text-align: center;
  border-radius: 1rem;
  font-weight: 900;
}
.incluye__body--titulo {
  text-align: left;
  font-weight: normal;
  background-color: #cacaca;
}
.incluye__body--clasico {
  color: #1D57C5;
  background-color: rgba(29, 87, 197, 0.3);
}
.incluye__body--premium {
  color: #eeb82c;
  background-color: rgba(238, 184, 44, 0.3);
}

.tutoriales {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
}
.tutoriales__h2 {
  font-size: 3rem;
  color: #1D57C5;
  font-weight: 900;
}
.tutoriales__texto {
  color: #00615A;
  font-size: 2rem;
  font-weight: 900;
  margin-top: 1rem;
}
.tutoriales__video {
  border-radius: 2rem;
  border: 3px solid #00615A;
  overflow: auto;
}

.wc_membresia {
  display: flex;
  gap: 1rem;
  align-items: center;
  border-radius: 1rem;
  padding: 2rem;
  color: #FFFFFF;
}
.wc_membresia p {
  margin: 0 !important;
}
.wc_membresia i {
  font-size: 2.5rem;
}
.wc_membresia--limite {
  background-color: #950f10;
}
.wc_membresia--activo {
  background-color: #004842;
}
.wc_membresia--expirado {
  background-color: #950f10;
}
.wc_membresia__texto {
  line-height: 1.5;
}
.wc_membresia__texto--disponible {
  font-size: 2.3rem;
  font-weight: 500;
}
.wc_membresia__texto--disponible span {
  font-weight: 900;
}